import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import BackButton from "../../components/common/BackButton";
import ProductCard from "../../components/features/product";
import market from "../../config/market";

export default function ChoosenProductList({ addClass }) {
	const { productList } = useSelector((state) => state.choosenProducts);
	const { t } = useTranslation();

	useEffect(() => {
		if (market.config.home === 5 && window.innerWidth < 1000) {
			document.querySelector(".header-six").classList.add("d-none");
		}
		return () => {
			if (market.config.home === 5 && window.innerWidth < 1000) {
				document
					.querySelector(".header-six")
					.classList.remove("d-none");
			}
		};
	}, []);

	return (
		<div className="choosen-product-list">
			<div className="container choosen-product-list_body">
				<div className="choosen-product-list_header">
					<BackButton
						icon={
							market.config.home === 5
								? market.config.icons.backArrow
								: null
						}
					/>
					<h3>{t("choosen")}</h3>
				</div>
				<div className="row p-0 m-0 mt-2">
					{productList.length > 0 ? (
						productList.map((product, i) => {
							return (
								<div
									key={i}
									className={
										addClass
											? addClass
											: "col-lg-3 col-md-4 col-6"
									}
								>
									<ProductCard product={product} />
								</div>
							);
						})
					) : (
						<h3 className="empty">{t("empty")}</h3>
					)}
				</div>
			</div>
		</div>
	);
}
